@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;500;600;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

:root {
  --title-font: 'Quicksand', sans-serif;
  --light-accent-color: #00E9AA;
  --dark-accent-color: #84FFC5;
  --accent-color: var(--dark-accent-color);

  --light-bg: white;
  --dark-bg: rgb(14, 14, 15);
  --bg: var(--light-bg);
  --cursor-color: black;
}

body {
  background: white;
  background: var(--bg);
  cursor: none;
}

h1 {
  font-family: 'Quicksand', sans-serif, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-family: var(--title-font), -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  ;

  font-weight: 800;
}
.cursor {
  width: 3rem;
  height: 3rem;
  border: 2px solid var(--cursor-color);
  border-radius: 50%;
  position: absolute;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -webkit-transform-origin: 100% 100%;
          transform-origin: 100% 100%;
  pointer-events: none;
  z-index: 200;
}

::-webkit-scrollbar {
  width: 5px;
  position: absolute;
}

/* Track */
::-webkit-scrollbar-track {
  background: #e6e6e6;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgba(132, 255, 198, 0.719);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

header .nav-shadow {
  box-shadow: 0 1px 8px 1px rgba(0, 0, 0, 0.295);
}

header nav {
  height: 80px;
  padding: 20px;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 100;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: var(--bg);
  -webkit-animation: fade 200ms forwards ease;
          animation: fade 200ms forwards ease;
}

header nav ul {
  list-style: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

header nav ul li {
  margin-left: 20px;
  padding: 15px 20px;
  transition: background 200ms ease;
}

header nav ul li:hover {
  background: var(--accent-color);
}

header nav ul li:hover .cursor {
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
}

header nav #header-logo {
  width: 50px;
}

@-webkit-keyframes fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes fade-left {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-20px);
            transform: translateX(-20px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@keyframes fade-left {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-20px);
            transform: translateX(-20px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

header .landing-content {
  display: flex;
  align-items: center;
  position: relative;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  position: relative;
}

header .landing-content img {
  width: 60%;
  margin-left: auto;
  -webkit-animation: fadein-img 300ms forwards ease;
          animation: fadein-img 300ms forwards ease;
}

header .landing-content .landing-message {
  padding: 20px;
  padding-bottom: 100px;
  -webkit-animation: fade-left 200ms forwards ease;
          animation: fade-left 200ms forwards ease;
}

header .landing-content .landing-message h1 {
  font-size: 3rem;
}

@-webkit-keyframes fadein-img {
  0% {
    opacity: 0;
    -webkit-transform: translateX(40px);
            transform: translateX(40px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
}

@keyframes fadein-img {
  0% {
    opacity: 0;
    -webkit-transform: translateX(40px);
            transform: translateX(40px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
}

@media (max-width: 600px) {
  header .landing-content {
    display: block;
  }
  header .landing-content img {
    width: 100%;
  }
  header .landing-content .landing-message {
    padding: 20px;
  }
}
